.card{
  border-radius: 5px;
  background-color: #ffffff;
  text-align: center;
  padding: 2rem 1.5rem;
  position: relative;
}

.card h3{
  color: var(--dark-violet);
  line-height: 3rem;
  margin-top: .5rem;
}

.card .img{
  position: absolute;
  top: -17%;
  left: 40%;
  background: var(--dark-violet);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.card img{
  width: 2.1rem;
  height: 2.1rem;
}

.vertical-line{
  background-color: var(--cyan);
  height: 5rem;
  width: 7px;
  margin: auto;
}